@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.App-footer {
  background-color: #282c34;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.clock-container {
	 position: relative;
	 width: 20%;
	 overflow: hidden;
	 background: #fff;
	 margin-left: auto;
	 margin-right: auto;
	 display: block;
}
 .clock-container.styling {
	 box-shadow: 0px 0px 10px #888;
	 border-radius: 50%;
	 min-width: 100px;
	 margin-top: 1%;
}
 .clock-container:before {
	 content: "";
	 display: block;
	 padding-top: 100%;
}
 .clock-content {
	 position: absolute;
	 top: 0;
	 left: 0;
	 bottom: 0;
	 right: 0;
	 display: flex;
	 justify-content: center;
	 align-items: center;
}
 .clock-content .background-numbers {
	 position: absolute;
	 width: 90%;
	 height: 90%;
	 top: 5%;
	 left: 5%;
	 z-index: 1;
}
 .clock-content .background-numbers .numbers {
	 fill: grey;
}
 .clock-content .indicator {
	 border-radius: 10px;
	 -webkit-transform-origin: bottom;
	         transform-origin: bottom;
	 outline: 1px solid transparent;
	 position: absolute;
	 bottom: 50%;
}
 .clock-content .indicator.seconds-indicator {
	 width: 0.5%;
	 height: 37.5%;
	 z-index: 2;
	 background-color: #dfc48c;
}
 .clock-content .indicator.minutes-indicator {
	 width: 1%;
	 height: 30%;
	 z-index: 3;
	 background-color: #cd6a51;
}
 .clock-content .indicator.hours-indicator {
	 width: 1.5%;
	 height: 22.5%;
	 z-index: 4;
	 background-color: #8f9c6c;
}
 .clock-content .indicator-cover {
	 position: absolute;
	 bottom: 48.5%;
	/*(50% - (3% /2)) */
	 border-radius: 50%;
	 width: 3%;
	 height: 3%;
	 background-color: lightgrey;
	 z-index: 5;
}
 .digital {
	 margin-top: 50px;
	 text-align: center;
	 font-family: 'Roboto', sans-serif;
	 font-size: 20px;
}
 
